import popup from '../../components/popup.vue'
export default {
  name: 'setting',
  data () {
    return {
		show:false,
		title:'请输入邀请码',
		key:'',
		// token:'',
		keystatus:0
    }
  },
  mounted(){
	  var that=this
	  uni.getStorage({
	      key: 'user',
	      success: function (res) {
	          that.keystatus=res.data.agentid
	      }
	  });
  },
  components:{
	popup
  },
  methods:{
	  invitioncode(){
		  this.show=true;
	  },
	  cancalFun(){
		  this.show=false;
	  },
	  confirmFun(){
		  var that=this
		  uni.request({
		  	url: getApp().globalData.APIurl+'/api/wxapp/member/bindActikey',
		  	data: {
		  	    key: that.key,
		  	},
		  	header: {
		  	    'Token': that.token ,
		  		'Device-Type':'wxapp'
		  	},
		  	success: (res) => {
		  		if(res.data.code){
		  			uni.showToast({
		  			    title: '绑定成功',
		  				icon:'none',
		  			    duration: 2000,
		  				success:function(){
		  					setTimeout(()=>{
								that.show=false;
								uni.setStorage({
								    key: 'user',
								    data:res.data.data,
								    success: function () {
								        console.log('success');
								    }
								});
								that.keystatus=res.data.data.agentid
		  					},2000)
		  					
		  				}
		  			});
		  		}else{
					uni.showToast({
					    title: '绑定失败',
						icon:'none',
					    duration: 2000,
						success:function(){
							
						}
					});
				}
		  	}
		  })
	  }
  }
}
